import type {NextRequest} from 'next/server';
import {NextResponse} from 'next/server';

export function middleware(request: NextRequest) {
		const url = request.nextUrl;
		
		const capitalizedPathname = url.pathname
				.split('/')
				.map((segment) => (segment ? segment.charAt(0).toUpperCase() + segment.slice(1) : segment))
				.join('/');
		
		// If the pathname needs capitalization, redirect to the capitalized version
		if (url.pathname !== capitalizedPathname) {
				url.pathname = capitalizedPathname;
				return NextResponse.redirect(url);
		}
		
		return NextResponse.next();
}
